@import "../../../../css/module/color";


body{
	overflow-x: hidden;
}

#tbSchedule {
	width: 100%;
	table-layout: fixed;
	border-collapse: collapse;
	border: 1px solid #ddd;
	border-radius: 9px;
	
	tr {
		> td {
			height: 60px;
			//width: 320px;
			line-height: 60px;
			border: 1px solid #eee;
			text-align: center;
			border-top: none;
			border-right: none;
			
			&:nth-of-type(4n+1) {
				width: 105px;
				font-size: 14px;
				border-left: none;
				color: #999;
				background-color: #fff;
				border-bottom: 1px solid #ddd;

				box-shadow: inset -1px 0 1px 0 rgba(0, 0, 0, 0.1);
			}
		}
	}
	
	thead {
		td {
			color: #fff;
			background-color: #c71111;
			span, time {
				display: inline-block;
			}
			> .x {
				width: 90px;
				height: 50px;
				vertical-align: middle;
				font-size: 29px;

			}
			> time {
				width: 100px;
				height: 50px;
				vertical-align: middle;
				text-align: left;
				padding-left: 1em;
				> .d {
					width: 99px;
					font-size: 16px;
				}
				> .dt {
					font-size: 12px;

				}
			}
			
			&:nth-of-type(4n+1) {
				font-size: 24px;
			}
		}
	}
	
	tbody {
		td {
			background-color: #fff;
			vertical-align: top;
			
			&.noTime {
				background-color: #ddd;
				color: #aaa;
			}
		}
		
		&.mine {
			td {
				//height: p2m(220);
				padding: 2px;
				
				section {
					display: flex;
					flex-direction: row;
					flex-wrap: nowrap;
					align-items: flex-start;
					align-content: stretch;
					justify-content: space-between;
				}
				div {
					width: 100%;
					height: 60px;
					padding: 6px;
					margin: 2px;
					border-radius: 5px;
					font-size: 12px;
					text-transform: capitalize;
					vertical-align: top;
					padding-top: 32px;
					cursor: pointer;
					background:{
						repeat: no-repeat;
						position: center 10px;
					};
				}
				.divAddTo {
					border: 1px dashed #d71711;
					color: #d71711;
					font-size: 14px;
					padding-top: 14px;
					b{
						font-size: 24px;
						display: block;
					}
				}

				.divAppointSpeed {
					background-image: url("./ico-appointmentSpeed.png?__inline");
					color: #f026f1;
					border: 1px solid #e6aff1;
					background-color: #fcedff;
					overflow:hidden;
					text-overflow:ellipsis;
					white-space:nowrap;
				}

				.divAppoint {
					background-image: url("./ico-appointment.png?__inline");
					color: #f1d27e;
					border: 1px solid #f1d27e;
					background-color: #fff8df;
					overflow:hidden;
					text-overflow:ellipsis;
					white-space:nowrap;
				}
				.divTempAppoint {
					background-image: url("./ico_tempappointment.png?__inline");
					color: #cccccc;
					border: 1px solid #eeeeee;
					background-color: #eeeeee;
					overflow:hidden;
					text-overflow:ellipsis;
					white-space:nowrap;
				}
				.divActivity {
					background-image: url("./ico-activity.png?__inline");
					color: #fd722d;
					border: 1px solid #fd722d;
					background-color: #ffdece;
					overflow:hidden;
					text-overflow:ellipsis;
					white-space:nowrap;
				}
				.divMeeting {
					background-image: url("./ico-meeting.png?__inline");
					color: #92be57;
					border: 1px solid #92be57;
					background-color: #e5eed7;
					overflow:hidden;
					text-overflow:ellipsis;
					white-space:nowrap;
				}
				.divWalk {
					background-image: url("./ico-walk.png?__inline");
					color: #617dc3;
					border: 1px solid #617dc3;
					background-color: #d7e3ee;
					overflow:hidden;
					text-overflow:ellipsis;
					white-space:nowrap;
				}
				.gray,&.gray{
					color: #bbb;
					border: 1px solid #ddd;
					background-color: #f6f6f6;
					filter: grayscale(100%);
					-webkit-filter: grayscale(100%);
				}
				
			}
		}
		
		&.rest {
			td {
				height: 32px;
				line-height: 32px;
				vertical-align: middle;

				&.lunch {
					background-color: #f0f0f0;
					color: #aaa;
				}
			}
		}
	}
	
}


h3{
	font-size: 16px;
}

b.red{
	color: $red;
	display: block;
}
p.ab{
	font-size: 12px;
}

a.aView{
	color: $red;
}

